body {
  font-family: "Noto Serif JP", serif;
  font-size: 400;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.title {
  font-size: 2rem;
  font-family: "Gotu", serif;
  line-height: 2.5rem;
}

.subtitle {
  color: #408F95;
  font-size: 0.75rem;
  line-height: 1.125rem;
}

.title__subtitle {
  padding-bottom: 1.5rem;
}